// import prosemirror modules
import { Schema, DOMParser } from "prosemirror-model";
import { EditorState } from "prosemirror-state";
import { EditorView } from "prosemirror-view";

import 'prosemirror-view/style/prosemirror.css';

// create a schema -> new Schema({nodes: doc:})
const mySchema = new Schema({
    nodes: {
        doc: {content: 'block+'},
        paragraph: {
            content: 'text*',
            toDOM(nodes) { return ['p',0] }
        },
        text: {}
    }
});
// create editor state (DOMParser.fromSchema)
let rootContent = document.querySelector('#root');
let state = new EditorState.create({mySchema});
// add plugins as needed - undo, redo
// create view component -> new EditorView('doc.querySelect',
// {state})
let view = new EditorView(rootContent, state);
// import/add style file for editor